            left-arrow
            title="余额明细"
            @click-left="onClickLeft"
        >    
        </van-nav-bar> -->
        <section v-if="!data || data.length == 0" class="none-list" >
            <p>抱歉，您目前没有收支记录</p>
            <img src="../../assets/images/pic_recording.png" />
        </section>
        <section v-if="data || data.length > 0">
            <van-list
           
                v-model="loading"
                :finished="finished"
                finished-text=""
                @load="onLoad"
            >
                <ul class="list" >
                    <li v-for="(item,key) in data" :key="key" >
                        <div class="flex justify-space-between">
                            <div class="left">
                                <p class="name">{{item.type_name}}</p>
                                <p>{{item.dateline}}</p>
                            </div>
                            <p class="right">{{item.is_add == 0 ? '-' : '+' }}{{item.price}}</p>
                        </div>
                        <van-divider />
                    </li>
                
                </ul>
            </van-list>
        </section>
    </div>
</template>

<script>
import { Toast } from 'vant';
export default {
    data(){
        return {
            data:'',
            loading:false,
            finished:false,
            pageInfo:{
                page_size:10,
                page_index:0
            }         
        }
    },
    created(){
        document.title = '余额明细'
        this.getUserWalletLog(this.uid);
    },
    methods:{
        onClickLeft(){
            this.$router.go(-1)
        },
        onLoad(){
            this.getUserWalletLog(this.uid);
        },
        getUserWalletLog(){
            this.api.getUserWalletLog({
                token:this.$store.state.userData,
                page_size:this.pageInfo.page_size,
                page_index:this.pageInfo.page_index
            }).then(res => {
      
                this.loading = false;
                if(res.err_code == 0){
                    this.pageInfo.page_index++;
                    if(!res.data.budget.length){
                        this.finished = true;
                    }
                    this.data = [...this.data,...res.data.budget] 
                }
            })
        },
        handleTx(){
            if(this.intBalance >= 2){

            }
        },

    }
}
</script>
