        p{
            font-size: 15px;
            color: #888888;
            margin-top: 65px;
            margin-bottom: 41px;
        }
        img{
            width:238px;
            height:185px;
        }
    }
    .list{
        padding: 15px;

        li{ 

            padding: 22px 0 0;
      
            .left{
                p{
